import { Component, Prop, h, Element, Method, State } from '@stencil/core';

/**
 * @category Overlays
 * @description A modal dialog component for displaying content that requires user interaction or attention. Features backdrop click handling, escape key support, and programmatic open/close control.
 *
 * @slot - Content placed inside of the dialog box
 */
@Component({
    tag: 'atui-dialog',
    styleUrl: 'atui-dialog.scss',
    shadow: false,
})
export class AtuiDialogComponent {
    @Element() el!: HTMLAtuiDialogElement;

    /**
     * ID of the dialog element (used to open and close the modal)
     */
    @Prop()
    dialog_id: string;

    /**
     * Role of the dialog element. Can be either 'dialog' or 'alertdialog'
     */
    @Prop()
    role: 'dialog' | 'alertdialog' = 'dialog';

    /**
     * Internal state to track if dialog is open
     */
    @State()
    isOpen: boolean = false;

    /**
     * Opens the dialog modal
     * @returns Promise that resolves when the dialog is opened
     */
    @Method()
    async openDialog(): Promise<void> {
        const dialog = this.el.querySelector('dialog') as HTMLDialogElement;
        if (dialog && !this.isOpen) {
            dialog.showModal();
            this.isOpen = true;
        }
    }

    /**
     * Closes the dialog modal
     * @returns Promise that resolves when the dialog is closed
     */
    @Method()
    async closeDialog(): Promise<void> {
        const dialog = this.el.querySelector('dialog') as HTMLDialogElement;
        if (dialog && this.isOpen) {
            dialog.close();
            this.isOpen = false;
            dialog.removeAttribute('open');
        }
    }

    private handleDialogClose = (event: Event) => {
        event.preventDefault();
        if (this.isOpen) {
            this.closeDialog();
        }
    };

    private handleKeyDown = (event: KeyboardEvent) => {
        if (event.key === 'Escape' && this.isOpen) {
            event.preventDefault();
            this.closeDialog();
        }
    };

    render() {
        return (
            <dialog
                class="c-atui-dialog"
                id={this.dialog_id}
                data-name="dialog"
                role={this.role}
                aria-modal="true"
                onClose={this.handleDialogClose}
                onKeyDown={this.handleKeyDown}
            >
                <div class="backdrop-content">
                    <slot />
                </div>
            </dialog>
        );
    }
}
